body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background-image: url('../assets/img/123.jpg');
  background-size: 100% ; */
/* background-color: white; */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



.buttonText{
    color: white;
    font-size: 18px;
    font-weight: 600;
}
.titleText{
    font-size: auto;
    font-weight: bold;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden; 
}

.brandName{
    font-weight: bold;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    display: -webkit-inline-box;
}

.autoWidth {
    width: auto;
    text-align: center;
}

._header{
    padding-right: 0px !important;
}


.container {
    display: flex;
    flex-direction: row;
    overflow: hidden;
    width: 100vw;
    flex-wrap: wrap;
    align-items: center;
    word-break: normal;
    justify-content: center;
    color: aqua;
}
.pageTitle {
    font-size: 36px;
    color: black;
    text-align: center;

}
.price {
    font-size: 36px;
    color: green;
    text-align: right;
    font-weight: bold;
    margin-right: 40px;
    width: auto;
}

.orderPrice{
    font-size: 24px;
    color: rgb(51, 32, 32);
    text-align: center;
    font-weight: bold;
}

.errorScreenComponent {
    flex-direction: row;
    justify-content: center;

}

@media (max-width: 1400px) and (min-height: 200px) {
    .container1 {
      display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
  }
}
